import React, { useCallback, useContext } from 'react';
import { StaticContext } from 'react-router';
import { RouteComponentProps } from 'react-router-dom';
import styles from './css/user.module.css';
import { ProfileData } from '../interfaces';
import { FaToggleOn, FaToggleOff } from 'react-icons/fa';
import { POST } from '../utils/requests';
import { AlertContext } from '../Contexts';
import * as regexes from '../utils/regexes';

export default function Accounts(props: RouteComponentProps<any, StaticContext, any> & {
	direction: string
	user: ProfileData
	updateProfile: () => void
}) {

	const toggleTheme = useCallback(() => POST({
		url: '/profile/updateSetting',
		data: {
			theme: props.user.theme === 'light' ? 'dark' : 'light'
		}
	}).then(props.updateProfile), [props.user.theme, props.updateProfile]);

	const updateBackground = useCallback((url: string) => POST({
		url: '/profile/updateSetting',
		data: {
			backgroundURL: url
		}
	}).then(props.updateProfile), [props.updateProfile]);

	const setAlert = useContext(AlertContext);

	return (
		<section className={['container', styles.container].join(' ')}>
			<div className={['header-button', styles.darkMode, 'switchContainer'].join(' ')}>
				<label>
					<div>
						{props.user.theme === 'light' ? 
							'Light Mode' :
							'Dark Mode'
						}
					</div>
				</label>
				<div className='toggleSwitch' onClick={toggleTheme}>
					{props.user.theme === 'light' ? 
						<FaToggleOn /> :
						<FaToggleOff />
					}
				</div>
			</div>
			<div className={['header-button', styles.darkMode, 'switchContainer'].join(' ')} onClick={() => {
				setAlert({
					title: 'Add background image',
					message: 'Add a link to the background image you want to use. Leave empty to clear.',
					type: 'prompt',
					resolve: updateBackground,
					prompt: {
						validate: (v: string | undefined) => v ? regexes.link.test(v) : true,
						errorMessage: 'Must be a valid link to an image',
						allowNull: true,
						defaultValue: props.user.backgroundURL
					}
				});
			}}>
				{props.user.backgroundURL ?
					<>
						<a href={props.user.backgroundURL} target='_blank' rel='noopener noreferrer'>
							Background set
						</a>{'\u200b'} | Change
					</> :
					'Add background image'
				}
			</div>
		</section>
	)
}